import classNames from 'classnames';
import { FC } from 'react';
import { useForm, FormProvider, UseFormRegister, DeepMap, FieldError } from 'react-hook-form';
import { AddressFormData } from '../../types';
import Button from '../common/button/button';
import Input, { InputOptions } from '../common/input/input';

interface Props {
	register: UseFormRegister<AddressFormData>;
	dirtyFields: DeepMap<AddressFormData, true>;
	errors: DeepMap<AddressFormData, FieldError>;
	inputClass: string;
}

interface InputDataNode {
	name: keyof AddressFormData;
	label: string;
	options: InputOptions;
	placeholder?: string;
}

const inputsData: InputDataNode[] = [
	{ name: 'firstName', label: 'imiƒô', options: { required: true } },
	{ name: 'lastName', label: 'nazwisko', options: { required: true } },
	{ name: 'email', label: 'e-mail', options: { required: true, type: 'email' } },
	{ name: 'address', label: 'adres', options: { required: true } },
	{ name: 'city', label: 'miasto', options: { required: true } },
	{ name: 'zip', label: 'kod pocztowy', options: { required: true } }
];
const AddressInputs: FC<Props> = ({register, dirtyFields, errors, inputClass}) => {
	return (
			<div className="form-grid">
				{inputsData.map(({ name, label, options, placeholder }) => (
					<Input
						name={name}
						label={label}
						options={options}
						placeholder={placeholder}
						register={register}
						isFilled={dirtyFields[name]}
						isInvalid={Boolean(errors[name])}
						className={inputClass}
					/>
				))}
			</div>
	);
};

export default AddressInputs;
