import classNames from 'classnames';

import './input.css';

import { FC } from 'react';
import { UseFormRegister, Validate, ValidationRule } from 'react-hook-form';
import { AllFormData } from '../../../types';

export type OnChange = (e: React.ChangeEvent<HTMLInputElement>) => void;
type InputTypesExNum = "button" | "checkbox" | "color" | "date" | "datetime-local" | "email" | "file" | "hidden" | "image" | "month" | "password" | "radio" | "range" | "reset" | "search" | "submit" | "tel" | "text" | "time" | "url" | "week";
type DataMng =
	| {
			register: UseFormRegister<any>;
			onChange?: never;
	  }
	| { register?: never; onChange: OnChange };

interface CommonOptions {
	required?: boolean;
	pattern?: ValidationRule<RegExp>;
}
type NumericOptions = {
	min?: number; // TODO object errors maybe?
	max?: number;
	step?: number;
};
type TextOptions = {
	maxLength?: number;
	minLength?: number;
};
export type InputOptions = (
	| ({
			type: "number"
	  } & NumericOptions)
	| ({
			type?: InputTypesExNum
	  } & TextOptions)
) &
	CommonOptions;

type Props = {
	name: keyof AllFormData;
	label: string;
	id?: string;
	// register: UseFormRegister<FormData>;
	className?: string;
	labelIn?: boolean;
	placeholder?: string;
	validate?: Validate<string | number> | Record<string, Validate<string | number>>;
	options: InputOptions;
	isFilled?: boolean;
	isInvalid?: boolean;
} & DataMng;

const Input: FC<Props> = ({
	name,
	label,
	register = () => {},
	placeholder,
	className = '',
	labelIn,
	options,
	onChange,
	isFilled,
	isInvalid,
	id,
}) => {
	// console.log(options);

	// const numeric = options?.numeric;
	console.log('reder');

	return (
		<div
			className={classNames('input-container appear-opacity appear-scale', className, {
				filled: isFilled,
				'input-container--label-in': labelIn
			})}>
			<input
				onChange={onChange}
				{...register(name, options)}
				id={id || name}
				type={options.type || 'text'}
				className={classNames('input', { 'input--error': isInvalid })}
				placeholder={placeholder}
			/>
			<label htmlFor={id || name} className="input-container__label">
				{label}
			</label>
		</div>
	);
};
export default Input;
