import { FC, ReactNode, useState } from 'react';
import { useForm, UseFormProps } from 'react-hook-form';
import { AddressFormData } from '../../../types';

import Stepper from '../../common/stepper/stepper';
import Form from '../form';
import PaymentForm from '../paymentForm';
import AddressInputs from '../addressInputs';
// import AddressForm from '../addressForm';
// import Confirmation from '../confirmation';
// import PaymentForm from '../paymentForm';

import './checkout.css';

// const formClass = 'checkout__form';
// const inputClass = 'checkout__input-container';
// const forms = [
// 	<AddressForm className={formClass} inputClass={inputClass} />,
// 	<PaymentForm className={formClass} inputClass={inputClass} />,
// 	<Confirmation className={formClass} inputClass={inputClass} />
// ];
const stepNames = ['Adres dostawy', 'P≈Çatno≈õƒá'];
const inputClass = 'checkout__input'
function useReturnWhatNeeded<T>(options: UseFormProps<T>) {
	const {
		register,
		handleSubmit,
		formState: { dirtyFields, errors }
	} = useForm<T>(options);
	return { register, handleSubmit, dirtyFields, errors };
}

const Checkout: FC = () => {
	const [active, setActive] = useState(0);
	const goNext = () => setActive(active + 1);
	const goBack = () => setActive(active - 1);
	const addressUFReturn = useReturnWhatNeeded<AddressFormData>({
		defaultValues: { address: '', city: '', email: '', firstName: '', lastName: '', zip: '' }
	});
	// {
	// 	// TODO make custom hook from it
	// 	register,
	// 	formState: { dirtyFields, errors },
	// 	handleSubmit
	// }
	const useFormReturns = [addressUFReturn];
	// const nextStep = () => setActive(active + 1);
	const stepNodes = [
		<AddressInputs {...addressUFReturn} inputClass={inputClass} />,
		<PaymentForm />
	];
	return (
		<main className="checkout paper" onSubmit={() => setActive(active + 1)}>
			<Stepper active={active} steps={stepNames} className="checkout__stepper" />
			<Form
				goBack={goBack}
				goNext={goNext}
				handleSubmit={useFormReturns[active].handleSubmit}
				stepName={stepNames[active]}
				active={active}
				content={stepNodes[active]}
			/>
			{/* <div className="checkout__row">
				<Input
					id="checkout__name"
					label="Imiƒô"
					type="text"
					containerClass="checkout__input-container"
					placeholder="placeholder"
				/>
				<Input
					id="checkout__surname"
					label="Nazwisko"
					type="text"
					containerClass="checkout__input-container"
				/>
			</div> */}
		</main>
	);
};

export default Checkout;
