import { FC, useEffect, useMemo, useRef, useState } from 'react';
import { useForm, UseFormRegister, useWatch } from 'react-hook-form';
import { Link, useLocation } from 'react-router-dom';

import BackgroundBlur from '../common/backround-blur/blur';
import Cart from './nav-buttons/buttons';
import Input, { OnChange } from '../common/input/input';
import { NavMenu, HeaderMenu } from './menu/menu';
import { HeadSearchData } from '../../types';
import { ReactComponent as Logo } from '../../assets/logo.svg';

import './header.css';

const menuStaggerStep = 0.1;
const menuItems = [
	['Sklep', '/shop'],
	['O nas', '/about'],
	['Kontakt', '/contact']
];
const searchOptions = {}; // TODO: fill in later
const searchExcludedUrls = ['/checkout', '/cart'];

const Header: FC = () => {
	const url = useLocation().pathname;
	const notInCheckout = !searchExcludedUrls.includes(url);

	const [show, setShow] = useState(false);
	const animForce = useRef(false);
	const animDuration = useMemo(() => menuItems.length * menuStaggerStep * 1000 * 2, []);
	useEffect(() => {
		animForce.current = true;
		const timeoutId = setTimeout(() => {
			animForce.current = false;
		}, animDuration);
		return () => clearTimeout(timeoutId);
	}, [show, animDuration]);

	const {
		register,
		handleSubmit,
		formState: { dirtyFields }
	} = useForm<HeadSearchData>({ defaultValues: { headSearch: '' } });
	console.log(dirtyFields);

	return (
		<header className="header white-overlay">
			<BackgroundBlur parentClass="header">
				<nav className="wrapper__nav white-overlay white-overlay--dense">
					<section className="flex nav__flex-top">
						<Link to="/" className="nav__logo">
							<Logo className="logo__svg appear-opacity popup" />
						</Link>
						<Cart />
					</section>
					<section className="flex nav__flex-bottom">
						<Input
							// onChange={inputOnChange}
							register={register}
							options={searchOptions}
							name="headSearch"
							id="navSearch"
							label="wyszukaj produkty"
							className="nav__input-container popup input-container--label-in"
							isFilled={dirtyFields.headSearch}
						/>
						<NavMenu
							{...{
								show,
								setShow,
								staggerStep: menuStaggerStep,
								items: menuItems
							}}
						/>
					</section>
				</nav>
			</BackgroundBlur>
			<HeaderMenu
				{...{
					show,
					staggerStep: menuStaggerStep,
					items: menuItems,
					inTransition: animForce.current
				}}
			/>
			{notInCheckout && (
				<Input
					// onChange={inputOnChange}
					register={register}
					options={searchOptions}
					name="headSearch"
					label="Wyszukaj produkty"
					className="header__input-container popup input-container--label-in"
					placeholder="placeholder"
					isFilled={dirtyFields.headSearch}
				/>
			)}
		</header>
	);
};

export default Header;
