/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import { confirmable, createConfirmation, ReactConfirmProps } from 'react-confirm';

import { useForm } from 'react-hook-form';
import { FC } from 'react';
import Input, { InputOptions } from '../input/input';
import Button from '../button/button';
import { AllFormData } from '../../../types';

import './prompt.css';

type BaseProps = {
	errorMessage?: string;
	name: keyof AllFormData;
	options: InputOptions;
	defaultValue?: string | number;
};

// interface NumberTypeProps extends BaseProps {
// 	type: 'number';
// 	inputOptions?: { min?: number; max?: number; step?: number };
// }
type Props = BaseProps & ReactConfirmProps & { confirmation: string };
const Prompt: FC<Props> = ({
	confirmation,
	proceed,
	dismiss,
	show,
	errorMessage = '',
	options,
	name,
	defaultValue
}) => {
	// const [value, setValue] = useState(defaultValue);
	// const [isError, setIsError] = useState(false);
	// const handleProceed = () => {
	// 	const isGood = checkIfGoodInput(value);
	// 	if (isGood) proceed(value);
	// 	else setIsError(!isGood);
	// };
	const {
		register,
		handleSubmit,
		formState: { errors, dirtyFields },
		watch
	} = useForm<AllFormData>({
		defaultValues: {
			[name]: defaultValue
		}
	});
	const error = errors[name];
	console.log('prompt');

	return (
		/* eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions */
		<div className="prompt-background" onClick={dismiss} style={show ? {} : { display: 'none' }}>
			{/* eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions */}
			<form
				// action="#"
				className="prompt"
				onClick={(e) => e.stopPropagation()}
				onSubmit={handleSubmit((data) => proceed(`${data[name]}`))}>
				<Input
					className="prompt__input"
					register={register}
					label={confirmation}
					name={name}
					options={options}
					isFilled={dirtyFields[name]}
					isInvalid={Boolean(error)}
					labelIn
				/>
				{/* TODO ErrorMessage component maybe? https://react-hook-dialog.com/api/useformstate/errormessage */}
				{errorMessage && <div className="prompt__error">{errors[name] && errorMessage}</div>}{' '}
				<div className="prompt__buttons">
					<Button onClick={dismiss}>Anuluj</Button>
					{/* <Button onClick={handleProceed}>Okej</Button> */}
					<input type="submit" className="btn popup appear-scale" />
					{/* <Button className="submit" onClick={() => handleSubmit((data) => console.log(data))}>
						Okej
					</Button> */}
				</div>
			</form>
		</div>
	);
};
type PromptFunc = { (confirmation: string, props: BaseProps): Promise<string> };
const prompt: PromptFunc = (confirmation, props) => {
	const confirm = createConfirmation(confirmable(Prompt));
	return confirm({ confirmation, ...props });
};

export default prompt;
